* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  font-family: 'Open-Sans', sans-serif; }

.f_opensans {
  font-family: 'Open-Sans', serif; }
  .f_opensans * {
    font-family: 'Open-Sans', serif; }

.f_neuzeit {
  font-family: 'Neuzeit', serif; }
  .f_neuzeit * {
    font-family: 'Neuzeit', serif; }

.f_neuzeitheavy {
  font-family: 'Neuzeit', serif; }
  .f_neuzeitheavy * {
    font-family: 'Neuzeit', serif; }

img.bg_img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px; }

.content_container {
  width: 100%;
  max-width: 840px;
  margin: 0 auto; }

#root {
  height: 100%; }
  #root > #main_wrapper {
    min-height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    position: relative;
    /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#cccccc+1,ffffff+25,ffffff+50,ffffff+75,cccccc+100 */
    background: #cccccc;
    /* Old browsers */
    /* FF3.6-15 */
    background: -webkit-linear-gradient(top, #cccccc 1%, white 25%, white 50%, white 75%, #cccccc 100%);
    /* Chrome10-25,Safari5.1-6 */
    background: -webkit-gradient(linear, left top, left bottom, color-stop(1%, #cccccc), color-stop(25%, white), color-stop(50%, white), color-stop(75%, white), to(#cccccc));
    background: -o-linear-gradient(top, #cccccc 1%, white 25%, white 50%, white 75%, #cccccc 100%);
    background: linear-gradient(to bottom, #cccccc 1%, white 25%, white 50%, white 75%, #cccccc 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#cccccc', endColorstr='#cccccc',GradientType=0 );
    /* IE6-9 */ }
    #root > #main_wrapper .header, #root > #main_wrapper .footer {
      z-index: 1; }
    #root > #main_wrapper #main_container {
      -ms-flex: 1 1;
      flex: 1 1;
      z-index: 0; }

